import PropTypes from 'prop-types'
import React from 'react'
import Tab from 'react-bootstrap/Tab'
import Nav from 'react-bootstrap/Nav'

const compatableVersionArray = [
  4.5,
  4.6,
  4.7,
  4.8,
  4.9,
  5.0,
  5.1,
  5.2,
  5.3,
  5.4,
  5.5
]

const fileIncludedArray = ['Wordpress plugin in .zip format', 'Plugin installation instructions', 'PHP', 'CSS', 'JavaScript']
const browserCompatibilityArray = [
  'E11',
  'Safari',
  'Firefox',
  'Opera',
  'Chrome'
]
const renderListing = (array) => {
  return array.map((element, index) => {
    return <li key={index}>{element}</li>
  })
}

const ProductDescriptionTab = ({ spaceBottomClass, productFullDesc }) => {
  return (
    <div className={`description-review-area ${spaceBottomClass}`}>
      <div className='container'>
        <div className='description-review-wrapper'>
          <Tab.Container defaultActiveKey='productDescription'>
            <Nav variant='pills' className='description-review-topbar'>
              {/* <Nav.Item>
                <Nav.Link eventKey="additionalInfo">
                  {" "}
                  Additional Information{" "}
                </Nav.Link>
              </Nav.Item> */}
              <Nav.Item>
                <Nav.Link eventKey='productDescription'>Description</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey='technicalSpecifications'>
                  Technical Specifications
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey='filesIncluded'>Files Included</Nav.Link>
              </Nav.Item>

              <Nav.Item>
                <Nav.Link eventKey='browserCompatibility'>
                  Browser Compatibility
                </Nav.Link>
              </Nav.Item>
            </Nav>

            <Tab.Content className='description-review-bottom'>
              <Tab.Pane eventKey='additionalInfo'>
                <div className='product-anotherinfo-wrapper'>
                  <ul>
                    <li>
                      <span>Weight</span> 400 g
                    </li>
                    <li>
                      <span>Dimensions</span>10 x 10 x 15 cm{' '}
                    </li>
                    <li>
                      <span>Materials</span> 60% cotton, 40% polyester
                    </li>
                    <li>
                      <span>Other Info</span> American heirloom jean shorts pug
                      seitan letterpress
                    </li>
                  </ul>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey='productDescription'>
                <div style={{ marginBottom: 20 }}>
                  <span>
                    The Keeper WordPress Plugin enables Keeper Plus for Business
                    account holders to do the following on their WP website:
                  </span>
                  <ul
                    style={{
                      listStyleType: 'disc',
                      listStylePosition: 'inside'
                    }}
                  >
                    <li>
                      Display Memorial Page Previews (profile image, full name
                      and date of death)
                    </li>
                    <li>Search bar to easily find a memorial page</li>
                    <li>
                      Display upcoming memorial events (connected to event pages
                      on your memorial pages)
                    </li>
                  </ul>
                </div>
                <div style={{ marginBottom: 20 }}>
                  <span>
                    Your purchase of the Keeper WordPress Plugin includes:
                  </span>
                  <ul
                    style={{
                      listStyleType: 'disc',
                      listStylePosition: 'inside'
                    }}
                  >
                    <li>1 year subscription to the Keeper WordPress Plugin</li>
                    <li>WordPress Plugin in .zip format</li>
                    <li>Plugin installation instructions</li>
                    <li>Access to technical support</li>
                    <li>Plugin updates</li>
                  </ul>
                </div>

                <div>
                  <span>
                    Upon successful checkout, you will receive a .zip folder
                    that contains the WordPress plugin, and a PDF document with
                    the full instructions via email.
                  </span>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey='technicalSpecifications'>
                <div>
                  <span>Compatible WordPress Versions:</span>
                  <ul
                    style={{
                      listStyleType: 'disc',
                      listStylePosition: 'inside'
                    }}
                  >
                    {renderListing(compatableVersionArray)}
                  </ul>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey='filesIncluded'>
                <div>
                  <ul
                    style={{
                      listStyleType: 'disc',
                      listStylePosition: 'inside'
                    }}
                  >
                    {renderListing(fileIncludedArray)}
                  </ul>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey='browserCompatibility'>
                <div>
                  <ul
                    style={{
                      listStyleType: 'disc',
                      listStylePosition: 'inside'
                    }}
                  >
                    {renderListing(browserCompatibilityArray)}
                  </ul>
                </div>
              </Tab.Pane>
            </Tab.Content>
          </Tab.Container>
        </div>
      </div>
    </div>
  )
}

ProductDescriptionTab.propTypes = {
  productFullDesc: PropTypes.string,
  spaceBottomClass: PropTypes.string
}

export default ProductDescriptionTab
