import PropTypes from 'prop-types'
import React, { useEffect } from 'react'
import MetaTags from 'react-meta-tags'
import { BreadcrumbsItem } from 'react-breadcrumbs-dynamic'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'

import LayoutOne from '../../layouts/LayoutOne'
import Breadcrumb from '../../wrappers/breadcrumb/Breadcrumb'
import ProductDescriptionTab from '../../wrappers/product/ProductDescriptionTab'
import ProductImageDescription from '../../wrappers/product/ProductImageDescription'

import { fetchProducts } from '../../redux/actions/productActions'

const Product = ({ location, product }) => {
  const { pathname } = location

  useEffect(() => {
    if (!product) {
      fetchProducts()
    }
  }, [])

  return (
    <>
      <MetaTags>
        <title>Keeper Memorials - Online Store | Product Page</title>
        <meta
          name='description'
          content='Product page of Keeper Memorials - Online Store'
        />
      </MetaTags>

      <BreadcrumbsItem to={process.env.PUBLIC_URL + '/'}>
        Product Page
      </BreadcrumbsItem>
      <BreadcrumbsItem to={process.env.PUBLIC_URL + pathname}>
        Shop Product
      </BreadcrumbsItem>

      <LayoutOne headerTop='visible'>
        {/* breadcrumb */}
        <Breadcrumb />

        {product ? (
          <>
            {/* product description with image */}
            <ProductImageDescription
              spaceTopClass='pt-100'
              spaceBottomClass='pb-100'
              product={product}
            />

            {/* product description tab */}
            {/* <ProductDescriptionTab
          spaceBottomClass="pb-90"
          productFullDesc={product.fullDescription}
        /> */}
            <ProductDescriptionTab
              spaceBottomClass='pb-90'
              productFullDesc={product.description}
            />
          </>
        ) : (
          <div className='row' style={{ margin: 50 }}>
            <div className='col-lg-12'>
              <div className='item-empty-area text-center'>
                <div className='item-empty-area__icon mb-30'>
                  <i className='pe-7s-less' />
                </div>
                <div className='item-empty-area__text'>
                  Invalid Product <br />{' '}
                  <Link to={process.env.PUBLIC_URL + '/shop-grid-standard'}>
                    Shop Now
                  </Link>
                </div>
              </div>
            </div>
          </div>
        )}
      </LayoutOne>
    </>
  )
}

Product.propTypes = {
  location: PropTypes.object,
  product: PropTypes.object
}

const mapStateToProps = (state, ownProps) => {
  const itemId = ownProps.match.params.id

  let productInfo

  productInfo = state.productData.products.filter(
    single => single.id === itemId
  )[0]

  return {
    product: productInfo
  }
}

export default connect(mapStateToProps)(Product)
